<style>
#sheet-pannel {
  background-color: #fff;
  padding: 16px 32px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.35)
}
#sheet-pannel .expand-transition {
  transition: all .3s ease;
  overflow: hidden;
}

#sheet-pannel .expand-enter, #sheet-pannel .expand-leave {
  height: 0%;
  opacity: 0;
}

#sheet-pannel .sheet-title {
  margin:0;
  font-size: 20px
}

.sheet-button {
  margin-left: 10px
}
</style>
<template>
  <div id="sheet-pannel">
    <span class="sheet-title">{{title}}</span>
    <slot name="sheet-button"></slot>
    <div id="sheet-zone" v-show="sheetshow" transition="expand">
      <slot name="sheet-zone"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ['title', 'sheetshow']
}
</script>



